body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  min-width: 320px;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;

  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* variables */
:root {
  --primary: #EA580C;
  --secondary: white;
}

/* reset */
body,p,a,ul,li {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
li {
  list-style-type: none;
}

/* base styles */
body {
  background: var(--secondary);
  overflow-x: hidden;
}

.button {
  background: none;
  border: 2px solid var(--primary);
  color: var(--primary);
  padding: 6px 12px;
  border-radius: 20px;
  text-transform: uppercase;
  font-weight: bold;
}

.button:hover {
  color: var(--secondary);
  background: var(--primary);
}

input, textarea {
  background: rgba(255,255,255,0.05);
  padding: 10px 16px;
  border-radius: 20px;
  border: 2px solid black;
  color: #f2f2f2;
}

/* fonts */
h1, h2, h3, h4 {
  color: black;
  font-weight: normal;
  line-height: 1.4em;
}

p, a, li {
  color: black;
  line-height: 1.4em;
  font-size: 1em;
}

h1, h3{
  font-size: 1.2em;
}

h2 {
  font-size: 1.6em;
}

h4 {
  font-size: 1.1em;
}

.leading {
  font-size: 1.1em;
}

/* mobile styles  360px*/
.grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}

/* small tablet styles */
@media screen and (min-width: 620px) {
}

/* large tablet & laptop styles */
@media screen and (min-width: 960px) {
  body{font-size: 18px;}
}

/* desktop styles */
@media screen and (min-width: 1200px) {
  body {
    font-size: 20px;
  }
  h2 {
    font-size: 1em;
  }

  p {
    font-size: 0.8em;
  }
}